async function connectwallet() { 
  if (window.ethereum) { 
  var web3 = new Web3(window.ethereum); 
  await window.ethereum.send('eth_requestAccounts'); 
  var accounts = await web3.eth.getAccounts(); 
  account = accounts[0]; 
  document.getElementById('wallet-address').textContent = account; 
  contract = new web3.eth.Contract(ABI, NFTCONTRACT);
  vaultcontract = new web3.eth.Contract(VAULTABI, STAKINGCONTRACT);
  }
}
async function mint() {
  if (window.ethereum) { 
    var _mintAmount = Number(document.querySelector("[name=amount]").value); 
    var mintRate = Number(await contract.methods.cost().call()); 
    var totalAmount = mintRate * _mintAmount; 
  contract.methods.mint(account, _mintAmount).send({ from: account, value: String(totalAmount) }); 
  }
}

async function stakeit() {
	var tokenids = Number(document.querySelector("[name=stkid]").value);
	vaultcontract.methods.stake([tokenids]).send({from: account});
}

async function unstakeit() {
	var tokenids = Number(document.querySelector("[name=stkid]").value);
	vaultcontract.methods.unstake([tokenids]).send({from: account});
}

async function claimit() {
	var tokenids = Number(document.querySelector("[name=claimid]").value);
	vaultcontract.methods.claim([tokenids]).send({from: account});
}

async function verify() {
	var getbalance = Number(await vaultcontract.methods.balanceOf(account).call());
	document.getElementById('stakedbalance').textContent = getbalance; 
}

class MELIAN extends Component {
		constructor(){
			super();
			this.state = {
				balance: [],
				nftdata: [],
			};
		}
		async componentDidMount() {
			await axios.get((endpoint + `?module=stats&action=tokensupply&contractaddress=${NFTCONTRACT}&apikey=${apikey}`))
		.then(outputa => {
            this.setState({
                balance:outputa.data
            })
            console.log(outputa.data)
        })

		await axios.get((endpoint + `?module=account&action=tokennfttx&contractaddress=${NFTCONTRACT}&page=1&offset=100&tag=latest&apikey=${apikey}`))
		.then(outputb => {
			const { result } = outputb.data
            this.setState({
                nftdata:result
            })
            console.log(outputb.data)
			});
	  }
	  render() {
		const {balance} = this.state;
		const {nftdata} = this.state;

        return(
        <div>
		    <nav id="nav-page">
      <input type="checkbox" id="check" />
      <label for="check" class="checkbtn">
        <i class="fas fa-bars"></i>
      </label>
      
      <ul id="nav-page">
        <li><Link to="/" id="nav-a">Home</Link></li>
        <li><Link to="/Story" id="nav-a">Story</Link></li>
        <li><Link to="/Races" id="nav-a">Races</Link></li>
        <li><Link to="/Roadmap" id="nav-a">Roadmap</Link></li>
        <li><Link to="/Utilities" id="nav-a">Utilities</Link></li>
        <li><Link to="/Team" id="nav-a">Team</Link></li>
        <li><Link to="/Faq" id="nav-a">Faq</Link></li>
      </ul>
            </nav>


            <section>
            <video id="background-video" autoPlay loop muted >
	  		<source src="https://raw.githubusercontent.com/moinhaider520/test-7/main/public/assets/index.mp4" type="video/mp4" />
      		</video>
			
			  
			<div className="wallet" style={{background:"transparent",width:"100%"}}>
			<Button onClick={connectwallet} 
			style={{marginBottom:"55px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "22px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>Connect Wallet</Button>
        <form class="form" style={{borderRadius:"15px",boxShadow:"1px 1px 15px #000000",background: "transparent", border: "2px solid #fff", padding:"10px",width:"600px",margin:"auto"}}>
        <h4 style={{color:"#FFFFFF",fontSize:"24px"}}>Mint Portal</h4>
        <div class="card" id='wallet-address' style={{marginTop:"3px",boxShadow:"1px 1px 4px #000000",background:"transparent",color:"#fff"}}>
          <label for="floatingInput" >Please Connect Your Wallet</label>
          </div>
          <div class="card" style={{marginTop:"3px",boxShadow:"1px 1px 4px #000000",background:"transparent",color:"#fff"}}>
          <input type="number" name="amount" defaultValue="1" min="1" max="5" style={{background:"transparent",color:"#fff",textAlign:"center",border:"1px solid #fff",borderRadius:"5px",width:"100%",padding:"10px 0px"}}/>
          <label >Please select the amount of NFTs to mint.</label><br />
          <Button onClick={mint} style={{margin:"20px 0px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "18px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>Buy/Mint!</Button>
          
		  </div>
        <label style={{color:"#FFFFFF"}}>Price 0.05 ETH each mint.</label><br />
		<label style={{color:"#FFFFFF"}}>Tokens Minted: {balance.result} / 7000</label>
      </form>
	  <br/> <br/>
	  <form class="form" style={{borderRadius:"15px",boxShadow:"1px 1px 15px #000000",background: "transparent", border: "2px solid #fff", padding:"10px",width:"600px",margin:"auto"}}>
        <h4 style={{color:"#FFFFFF",fontSize:"24px"}}>Staking Portal</h4>
		
          <div class="card" style={{marginTop:"3px",boxShadow:"1px 1px 4px #000000",background:"Transparent",color:"#fff"}}>
          <input type="number" name="stkid"  style={{background:"transparent",color:"#fff",textAlign:"center",border:"1px solid #fff",borderRadius:"5px",width:"100%",padding:"10px 0px"}}/>
          <label >Please Enter Your NFT Token ID</label><br />
          <Button onClick={stakeit} style={{margin:"20px 5px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "18px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>Stake NFT</Button>
          <Button onClick={unstakeit} style={{margin:"10px 5px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "18px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>UnStake NFT</Button>
		  </div>
      </form><br /><br />
	  <form class="form" style={{borderRadius:"15px",boxShadow:"1px 1px 15px #000000",background: "transparent", border: "2px solid #fff", padding:"10px",marginTop:"50px",width:"600px",margin:"auto"}}>
        <h4 style={{color:"#FFFFFF",fontSize:"24px"}}>Rewards Portal</h4>
          <div class="card" style={{marginTop:"3px",boxShadow:"1px 1px 4px #000000",background:"transparent"}}>
          

		  <div class="card" id='stakedbalance' style={{marginTop:"3px",boxShadow:"1px 1px 4px #000000",background:"transparent",color:"#fff"}}>
		  <Button onClick={verify} style={{margin:"20px 0px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "18px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>Click Here To Verify Number of Staked NFTs</Button><br />
		  <label for="floatingInput"></label><br />
		  <input type="number"  name="claimid" style={{background:"transparent",color:"#fff",textAlign:"center",border:"1px solid #fff",borderRadius:"5px",width:"100%",padding:"10px 0px"}}/>
          <label >Please Enter Your TOKEN ID</label><br />
		  <Button onClick={claimit} style={{margin:"20px 0px",color:"#FFFFFF",background: "transparent",border: "2px solid #fff",fontSize: "18px",outline:"none",cursor:"pointer",padding:"10px 20px"}}>Claim Reward</Button>
          </div>

		  </div>
      </form>
      <div className="mt-3" style={{width:"100%",left: "0",position:"absolute"}}>
	  <div  style={{display: "inline-grid",gridTemplateColumns: "repeat(4, 5fr)",columnGap: "50px"}}>
  {nftdata.map(result => {
	  return (
			<div className="card" style={{width:"350px",marginTop:"20px",marginBottom:"20px",background:"#fff"}}>
            		<div className="image-over">
					<img className="card-img-top" style={{width:"350px",height:"350px"}} src={nftpng + result.tokenID +'.png'} alt="" />
					</div>
					<div className="card-caption col-12 p-0">
                    	<div className="card-body">
							<h5 className="mb-0">Valarok Collection NFT #{result.tokenID}</h5>
							<h6 className="mb-0" style={{fontSize:"14px"}} >Minted By: <p>{result.to}</p></h6>
					</div>
                </div>
            </div>
        );
    })}
</div>
</div>
          	</div>
		  </section>
		   </div>
        );
    };
}
export default MELIAN;