import './App.css';
import Home from './pages/home';
import Races from './pages/races';
import MELIAN from './pages/melian';
import {Switch, Route} from 'react-router-dom';
function App() {
  return (
    <div className="App">
     
     <Switch>
        
          <Route exact path="/"><Home/></Route>
          <Route exact path="/races"><Races/></Route>
          <Route exact path="/melian"><MELIAN/></Route>
        
    </Switch>
    
    </div>
  );
}

export default App;
